.hero{
    position: relative;
    width: 100%;
    padding-top: 295px;
    padding-bottom: 209px;
    margin-bottom: -4px;
    overflow: hidden;
    @include windowMax1440p{
        padding-top: 235px;
        padding-bottom: 160px;
    }
    .hero-language-wrap{
        width: 163px;
        height: 62px;
        background-color: var(--white-color);
        box-shadow: 0px 0px 0px 40px var(--white-color);
        @include windowMax1440p{
            width: 140px;
            height: 52px;
        }
    }
    .container{
        height: fit-content;
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        //box-shadow: 0px 0px 0px 1000px var(--white-color);
        .hero__wrap{
            overflow: hidden;
            //box-shadow: 0px 0px 0px 300px var(--white-color);
            &__content{
                display: flex;
            }
        }
        .big_text{
            //background-color: var(--white-color);
            //box-shadow: 0px 0px 0px 400px var(--white-color);
            z-index: 10;
            p{
                @include dmsans92();
                //overflow: hidden;
                //height: 100px;
                @include windowMax1440p {
                    @include dmsans80();
                    //height: 88px;
                }
            }
            .hero__wrap__content{
                width: 650px;
                height: 100px;
                transform: translateY(100px);
                @include windowMax1440p {
                    width: 550px;
                    height: 88px;
                }
            }
            span{
                @include gradient();
            }
        }
     .numbers{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            height: 100%;
            position: absolute;
            right: 0;
            box-shadow: 0px 0px 0px 3000px var(--white-color);
            @include windowMax1440p {
                margin-top: 16px;
                height: calc(100% - 16px);
            }
            --numbersWrapTop: 0px;
            &__wrap{
                overflow: hidden;
                position: relative;
                &::before{
                    content: "";
                    position: absolute;
                    top: var(--numbersWrapTop);
                    width: 100%;
                    height: calc(100% + 3px);
                    background-color: var(--white-color);
                    //background-color: wheat;
                    z-index: 1;
                    @include windowMax1440p {
                        height: calc(100% + 23px);
                    }
                }
                &::after{
                    content: "";
                    position: absolute;
                    bottom: -8px;
                    width: 100%;
                    height: 10px;
                    background-color: var(--white-color);
                }
                box-shadow:  0px 0px 0px 120px var(--white-color);
                @include windowMax1440p {
                    box-shadow:  0px 0px 0px 90px var(--white-color);
                }
            }
            &__wrap-top{
                margin-top: -2px;
                
            }
            &__wrap-bottom{
                margin-bottom: -2px;
            }

            &__box{
                display: flex;
                align-items: center;
                gap: 36px;
                transform: translateY(100px);
                img{
                    height: 100px;
                    //box-shadow: 0px 0px 0px 112px var(--white-color);
                    margin-left: -1px;
                    @include windowMax1440p {
                        height: 88px;
                        //box-shadow: 0px 0px 0px 82px var(--white-color);
                    }
                }
                #top_img{
                    margin-top: -1px;
                }

                #bottom_img{
                    margin-bottom: -1px;
                    //box-shadow: -100px 0px 0px 0px var(--white-color);
                }
            }
            p{
                @include dmsans24_500();
                @include windowMax1440p {
                    @include dmsans20_500();
                }
            }
            
        }
    }
}