.bigtext{
    //176px je o kolik se zmenší box v předchozí sekci
    height: calc(100vh - 200px - 256px);
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-color: var(--white-color);
    margin-top: -1px;
    //box-shadow: 0 200px 0  var(--white-color);
    @include windowMax1440p{
        height: calc(100vh - 148px - 231px);
    }
    p{
        @include dmsans92();
        @include windowMax1440p {
            @include dmsans80();
        }
    }
    span{
        @include gradient();
    }
}