.reasons {
    background-color: var(--white-color);
    //min-height: 1319px;

    //padding-bottom: 200px;
    .swap-wrapper-reasons {
        width: 100%;
        position: relative;
    }

    .headline-content {
        height: 248px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
        @include windowMax1440p {
            height: 224px;
        }
    }

    .container {
        padding-top: 200px;

        @include windowMax1440p {
            padding-top: 180px;
        }

        .headline {
            height: 544px;
            @include windowMax1440p {
                height: 416px;
            }

            h1 {
                @include dmsans92();
                padding-bottom: 48px;

                @include windowMax1440p {
                    @include dmsans80();
                }
            }

            p {
                @include dmsans28();
                padding-bottom: 200px;
                color: #6B727A;

                @include windowMax1440p {
                    font-size: 20px;
                    line-height: 40px;
                }
            }

            span {
                @include gradient();
            }
        }

        .bulletpoints {
            width: 100%;
            padding-bottom: 1px;

            p {
                transition: 0.3s ease-out;
                @include dmsans32();
                max-width: 1330px;

                @include windowMax1440p {
                    font-size: 24px;
                    line-height: 40px;
                }
            }

            .row {
                min-height: 112px;
                width: 100%;
                border-top: 1px solid #DFDFDF;
                display: flex;
                align-items: center;
                position: relative;
                @include windowMax1440p{
                    min-height: 106px;
                }

                .dot {
                    transition: 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940);
                    width: 21px;
                    height: 21px;
                    border-radius: 100%;
                    background: linear-gradient(90deg, var(--brown-color), var(--bronze-color));
                    opacity: 0;
                    position: absolute;
                }
            }

            .row:hover {
                p {
                    transform: translateX(54px);
                }

                .dot {
                    opacity: 100;
                }
            }

            .last {
                border-bottom: 1px solid #DFDFDF;
            }

            .lastlast {
                border-top: 0px;
                border-bottom: 1px solid #DFDFDF;
            }

            .lastlast.untoggled_service {
                display: none;
            }
        }
    }
}