.nav__left {
  max-width: calc((100vw - 1330px) / 2);
  min-width: 216px;
  width: 100%;
  height: 100%;
}

.nav__middle {
  max-width: 1330px;
  width: 100%;
  display: flex;
}

.nav__right {
  max-width: calc((100vw - 1330px) / 2);
  min-width: 216px;
  width: 100%;
  height: 100%;
}

.sb__nav__left{
  background-color: var(--colorLeft);
}

.sb__nav__right{
  background-color: var(--colorRight);
}

.fixed {
  position: fixed;
  width: 100%;
  max-height: 80px;
  overflow: hidden;
  --afterTop: 0;
  --afterHeight: 100%;

  @include windowMax1440p {
    max-height: 74px;
  }

  &::after {
    content: "";
    position: absolute;
    z-index: 100000;
    width: 100%;
    height: var(--afterHeight);
    top: var(--afterTop);
    background-color: #ffffff;
  }

  .header {
    height: 79px;
    width: 100%;
    background-color: transparent;
    position: relative;
    display: flex;
    border-bottom: 1px solid var(--border-color);
    color: white;
    z-index: 99;

    @include windowMax1440p {
      max-height: 73px;
    }

    .logo {
      display: flex;
      justify-content: center;
      align-items: center;

      img {
        cursor: pointer;
      }
    }

    .container_nav {
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-left: 1px solid var(--border-color);
      border-right: 1px solid var(--border-color);

      .language_switcher {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 30px;
        width: 74px;
        height: 40px;
        border: 1px solid var(--border-color);
        border-radius: 500px;
        cursor: pointer;

        button {
          width: 32px;
          height: 32px;
          border-radius: 500px;
          color: var(--white-color);
          z-index: 1;
          transition: 0.3s color;
          cursor: pointer;
          @include dmsans12_500();
        }

        .active {
          color: #341A0C;
        }

        .circle {
          position: absolute;
          border-radius: 100%;
          width: 32px;
          height: 32px;
          background-color: var(--white-color);
          color: #341A0C;
          //margin-right: 32px;
          left: 4px;
          cursor: pointer;
          transform: translateX(-50%);
          transition: 0.3s transform;

          &.clicked {
            transform: translateX(50%);
          }
        }
      }

      .services_nav {
        padding-right: 64px;
        display: flex;
        align-items: center;
        cursor: pointer;
        @include ibmplex();
        @include windowMax1440p{
          @include ibmplex14();
        }
        p {
          margin-left: 16px;
        }
      }

    }

    .cta {
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      background: rgba($color: #000000, $alpha: 0.4);
      @include ibmplex();
      @include windowMax1440p{
        @include ibmplex14();
      }
      transition: 0.3s background-color;

      &:hover {
        background-color: rgba($color: #000000, $alpha: 0.8);
      }
    }
  }

  .nav-language-wrap {
    width: 65px;
    height: 21px;
  }

  .service_menu_fixed {
    width: 100%;
    height: 119px;
    display: flex;
    justify-content: center;
    border-bottom: 1px solid var(--border-color);

    @include windowMax1440p {
      max-height: 74px;
    }

    cursor: pointer;

    .service_box_fixed {
      transition: .5s ease-out, color .5s ease-out;
      width: 50%;
      height: 100%;
      color: white;
      display: flex;
      align-items: center;
      position: relative;
      justify-content: center;

      .service_header_fixed {
        display: inline;
      }
    }
    .text {
      font-size: 32px;
      @include windowMax1440p{
        font-size: 26.664px;
      }
      p{
        @include dmsans48_500();
        font-size: 1em;
      }
      span{
          @include dmsans48();
          font-size: 1em;
      }
    }
    
    .right {
      display: flex;
      .text {
        //padding-left: 64px;
      }
    }


    .notactive {
      background: #ffffffe0;

      //border-bottom: 1px solid #ffffffe0;
      .text {
        color: var(--black-color);
      }
    }
  }
}